@import url('https://fonts.cdnfonts.com/css/noto-sans-bassa-vah');

.home-banner {
    width: 100%;
    height: 80vh;
}

.home-banner img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.countdown {
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 1.5em;
    margin: 2rem 0 2rem 0;
}

.countdown h2 {
    font-family: "Inter", Sans-serif;
    margin-right: 5rem;
    margin-top: 1.5rem;
    color: #82A0F4;
    text-transform: uppercase;
    font-weight: 800;
}

.colon {
    font-size: 2.5rem;
    color: #000;
    margin: 0 10px;
}

.countdown-timer {
    display: flex;
    justify-content: center;
}

.countdown-timer span {
    margin: 0 10px;
}

/* Style for each time unit (days, hours, minutes, seconds) */
.countdown-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 10px;
}

/* Increase the size of the number and set specific colors */
.countdown-item span:first-child {
    font-size: 3rem;
    font-weight: bold;
}

/* Style for the label (Days, Hours, etc.) */
.countdown-item span:last-child {
    font-size: 1.5rem;
    font-weight: 300;
}

/* Color the days item */
.countdown-item:nth-child(1) span:first-child {
    color: #82A0F4;
}

/* Color the hours item */
.countdown-item:nth-child(3) span:first-child {
    color: #56C1B6;
}

/* Color the minutes item */
.countdown-item:nth-child(5) span:first-child {
    color: #FE5F55;
}

/* Color the seconds item */
.countdown-item:nth-child(7) span:first-child {
    color: #B0EAE3;
}

/* Medium screens - Tablets */
@media screen and (min-width: 768px) and (max-width: 1024px) {

    .home-section {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        text-align: center;
    }

    .home-banner {
        height: 40vh;
        width: 100%;
    }

    .home-banner img {
        object-fit: contain;
    }

    .countdown {
        flex-direction: column;
        align-items: center;
        margin: 1rem 0;
        /* Reduced margin for better fit */
    }

    .countdown h2 {
        margin: 0;
        font-size: 1.2em;
        /* Adjust font size */
    }

    .colon {
        font-size: 2rem;
        /* Slightly smaller colons */
    }

    .countdown-item span:first-child {
        font-size: 2.5rem;
        /* Slightly smaller numbers */
    }

    .countdown-item span:last-child {
        font-size: 1.2rem;
        /* Slightly smaller labels */
    }
}

/* Small devices - Phones */
@media screen and (max-width: 600px) {

    .home-section {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        text-align: center;
    }

    .home-banner {
        height: 100%;
        width: auto;
        /* Takes full screen height */
    }

    .countdown {
        flex-direction: column;
        align-items: center;
        /* margin: 1rem 0; */
        font-size: 1.2em;
        /* Adjusted font size */
    }

    .countdown h2 {
        font-size: 1.1em;
        /* Smaller heading */
        margin: 0;
    }

    .countdown-timer {
        flex-direction: row;
        /* Stack countdown items vertically */
    }

    .colon {
        /* display: none; */
        font-size: 2.25rem;
        /* Remove colons for small screens */
    }

    .countdown-item {
        margin: 0.5rem 0;
        /* Space between countdown items */
    }

    .countdown-item span:first-child {
        font-size: 2rem;
        /* Smaller numbers */
    }

    .countdown-item span:last-child {
        font-size: 1rem;
        /* Smaller labels */
    }
}